const detectElementNode = (
  data,
  period = 500,
  breakTimeout = 280000,
  fnTest = null
) => {
  let breakPoint;
  if (breakTimeout) setTimeout(() => (breakPoint = true), breakTimeout);
  return new Promise((resolve, reject) => {
    const interval = setInterval(async () => {
      try {
        console.log(breakPoint, data.m3u8List, m3u8List);
        if (
          (data.m3u8List && data.headersID) ||
          (m3u8List && headersID) ||
          (fnTest !== null && fnTest())
        ) {
          clearInterval(interval);
          resolve(1);
        }
        if (breakPoint === true) {
          clearInterval(interval);
          resolve(2);
        }
        // keep on waiting
      } catch (error) {
        clearInterval(interval);
        reject(Error("failz"));
      }
    }, period);
  });
};

async function openSerie(params) {
  let browser,
    page,
    checkResult,
    resultSeason,
    resultEpisode,
    objSerie = [];
  const { pageLink } = params;
  let events_list = [];
  try {
    browser = await puppeteer.launch({ headless: "new" });
    page = await browser.newPage();
    //open page
    await page.goto(pageLink, { waitUntil: "networkidle2" });
    /* seasons */
    // search elements
    checkResult = await page
      .waitForSelector(".serie-menu .tt_season ul li a.active",{timeout:defaultTimeout})
      .then((el) => el)
      .catch((err) => false);
    if (!checkResult) throw new Error("Missing seasons");
    // get elements
    checkResult = await page
      .$$eval(".serie-menu .tt_season ul li a", (els) => {
        return Array.from(els).map((el) => {
          return { num: el.innerText, href: el.href.split("#")[1] };
        });
      })
      .catch((err) => false);
    if (!checkResult) throw new Error("Data seasons incomplete");
    resultSeason = checkResult;
    /* episodes */
    // search elements
    checkResult = await page
      .waitForSelector(`.serie-menu .tt_series .tab-pane ul li.active`,{timeout:defaultTimeout})
      .then((el) => el)
      .catch((err) => false);
    if (!checkResult) throw new Error("Missing episodes");
    for (let season of resultSeason) {
      // episodes
      let episodes = await page
        .$$eval(
          `.serie-menu .tt_series .tab-pane#${season?.href} ul li`,
          (els) => {
            return Array.from(els).map((el) => {
              let elem = {};
              const tempList = el.querySelectorAll("a[data-title]");
              elem = Object.assign(
                {},
                Array.from(tempList).map((tel) => {
                  return {
                    serie: tel.id,
                    title: tel.dataset.title,
                    num: tel.dataset.num,
                  };
                })
              )[0];
              const tempSubList = el.querySelectorAll(".mirrors a.mr");
              elem.links = Object.assign(
                {},
                Array.from(tempSubList).map((tsel) => {
                  return { host: tsel.dataset.m, link: tsel.dataset.link };
                })
              );
              return elem;
            });
          }
        )
        .catch((err) => false);

      if (episodes) {
        objSerie.push({ season, episodes });
      }
    }
    page.close();
    events_list = objSerie ? objSerie : ["false"];

    return { events_list };
  } catch (error) {
    page?.close();
    console.log("scrapeEvents::ERROR->", error);
    return false;
  } finally {
    browser?.close();
  }
}
async function openFilm(params) {
  let browser, page, checkResult, resultLinks;
  objFilm = [];
  const { pageLink } = params;
  let events_list = [];
  try {
    browser = await puppeteer.launch({
      headless: "new",
      args: [
        "--no-sandbox",
        "--disable-setuid-sandbox",
        "--user-agent=Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/80.0.3987.132 Safari/537.36",
      ],
    });
    page = await browser.newPage();
    //open page
    await page.goto(pageLink, { waitUntil: "networkidle2" });

    checkResult = await page
      .waitForSelector(`iframe[src*="mostra"]`,{timeout:defaultTimeout})
      .then((el) => el)
      .catch((err) => false);
    if (!checkResult) throw new Error("Missing iframe");
    // get elements
    checkResult = await page
      .$eval(`iframe[src*="mostra"]`, (el) => {
        return el?.src;
      })
      .catch((err) => false);
    if (!checkResult) throw new Error("Missing iframe src");
    // search elements
    //open page
    await page.goto(checkResult, { waitUntil: "networkidle2" });
    checkResult = await page
      .waitForSelector("li[data-link]",{timeout:defaultTimeout})
      .then((el) => el)
      .catch((err) => false);
    if (!checkResult) throw new Error("Missing links");
    // get elements
    checkResult = await page
      .$$eval("li[data-link]", (els) => {
        return Array.from(els).map((el) => {
          const data_link =
            el?.dataset?.link && !el?.dataset?.link?.includes("http")
              ? `https:${el?.dataset?.link}`
              : el?.dataset?.link;
          return { data_link };
        });
      })
      .catch((err) => false);

    resultLinks = checkResult;

    if (!checkResult) throw new Error("Missing links");

    for (let link of resultLinks) {
      if (
        link?.data_link?.includes("supervideo") ||
        link?.data_link?.includes("dropload")
      ) {
        objFilm.push({ link: link?.data_link });
      }
    }
    page.close();
    events_list = objFilm ? objFilm : ["false"];

    return { events_list };
  } catch (error) {
    page?.close();
    console.log("scrapeEvents::ERROR->", error);
    return false;
  } finally {
    browser?.close();
  }
}
