async function scrapeFilm(url) {
  m3u8List = null;
  headersID = null;
  let ob = [];
  let browser, page;
  try {
    browser = await puppeteer.launch({
      headless: "new",
      args: [
        "--no-sandbox",
        "--disable-setuid-sandbox",
        "--user-agent=Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/80.0.3987.132 Safari/537.36",
      ],
    });
    page = await browser.newPage();

    page.setDefaultNavigationTimeout(300000);

    const results = []; // collects all results

    let paused = false;
    let pausedRequests = [];

    const nextRequest = () => {
      // continue the next request or "unpause"
      if (pausedRequests.length === 0) {
        paused = false;
      } else {
        // continue first request in "queue"
        pausedRequests.shift()(); // calls the request.continue function
      }
    };

    await page.setRequestInterception(true);

    page.on("request", (request) => {
      if (paused) {
        pausedRequests.push(() => request.continue());
      } else {
        paused = true; // pause, as we are processing a request now
        request.continue();
      }
    });

    page.on("requestfinished", async (request) => {
      const response = await request.response();

      const responseHeaders = response.headers();
      const information = {
        url: request.url(),
      };
      results.push(information);

      nextRequest(); // continue with next request
    });

    page.on("requestfailed", (request) => {
      // handle failed request

      nextRequest();
    });

    await page.goto(url, { waitUntil: "networkidle2" });

    if(!m3u8List){
      const playerSrc = await page.evaluate(() => {
      
        try {
        
          return player ? player.getConfig().sources[0].file : false;
        } catch (err) {
          return false;
        }
      });
      console.log('Player:',playerSrc)
    if(playerSrc)
          m3u8List = playerSrc
  }

    for (request of results) {
      if (request.url.includes("/embed")) headersID = request.url;
      if (request.url.includes("/master.m3u8")) m3u8List = request.url;
    }

    console.log({ m3u8List, headersID });
    return { m3u8List, headersID: headersID ? headersID : url };
  } catch (error) {
    console.log("scrapeSerie::ERROR->", error);
    return false;
  } finally {
    page?.close();
    browser?.close();
  }
}

async function proxyM3U8(url, headers, res) {
  const newHeaders = headers;
  newHeaders["Accept-Encoding"] = "gzip, deflate, br";
  newHeaders["User-Agent"] =
    "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/80.0.3987.132 Safari/537.36";
  
  let req = await axios(url, {
    headers: newHeaders,
  }).catch((err) => {
    console.log("axios", err.message);
    return null;
  });

  if (!req) return;

  const m3u8 = req && req?.data;

  if (m3u8.includes("RESOLUTION=")) {
    // Deals with the master m3u8 and replaces all sub-m3u8 files (quality m3u8 files basically) to use the m3u8 proxy.
    // So if there is 360p, 480p, etc. Instead, the URL's of those m3u8 files will be replaced with the proxy URL.
    const lines = m3u8.split("\n");
    const newLines = [];
    for (const line of lines) {
      if (line.startsWith("#")) {
        if (line.startsWith("#EXT-X-KEY:")) {
          const regex = /https?:\/\/[^\""\s]+/g;
          const url = `${web_server_url}${
            "/ts-proxy?url=" +
            encodeURIComponent(regex.exec(line)?.[0] ?? "") +
            "&headers=" +
            encodeURIComponent(JSON.stringify(headers))
          }`;
          newLines.push(line.replace(regex, url));
        } else {
          newLines.push(line);
        }
      } else {
        const uri = new URL(line, url);

        newLines.push(
          `${
            web_server_url +
            "/m3u8-proxy?url=" +
            encodeURIComponent(uri.href) +
            "&headers=" +
            encodeURIComponent(JSON.stringify(headers))
          }`
        );
      }
    }

    [
      "Access-Control-Allow-Origin",
      "Access-Control-Allow-Methods",
      "Access-Control-Allow-Headers",
      "Access-Control-Max-Age",
      "Access-Control-Allow-Credentials",
      "Access-Control-Expose-Headers",
      "Access-Control-Request-Method",
      "Access-Control-Request-Headers",
      "Origin",
      "Vary",
      "Referer",
      "Server",
      "x-cache",
      "via",
      "x-amz-cf-pop",
      "x-amz-cf-id",
    ].map((header) => res.removeHeader(header));

    // You need these headers so that the client recognizes the response as an m3u8.
    res.setHeader("Content-Type", "application/vnd.apple.mpegurl");
    res.setHeader("Access-Control-Allow-Origin", "*");
    res.setHeader("Access-Control-Allow-Headers", "*");
    res.setHeader("Access-Control-Allow-Methods", "*");

    res.end(newLines.join("\n"));
    return;
  } else {
    // Deals with each individual quality. Replaces the TS files with the proxy URL.
    const lines = m3u8.split("\n");
    const newLines = [];
    for (const line of lines) {
      if (line.startsWith("#")) {
        if (line.startsWith("#EXT-X-KEY:")) {
          const regex = /https?:\/\/[^\""\s]+/g;
          const url = `${web_server_url}${
            "/ts-proxy?url=" +
            encodeURIComponent(regex.exec(line)?.[0] ?? "") +
            "&headers=" +
            encodeURIComponent(JSON.stringify(headers))
          }`;
          newLines.push(line.replace(regex, url));
        } else {
          newLines.push(line);
        }
      } else {
        const uri = new URL(line, url);
        // CORS is needed since the TS files are not on the same domain as the client.
        // This replaces each TS file to use a TS proxy with the headers attached.
        // So each TS request will use the headers inputted to the proxy
        newLines.push(
          `${web_server_url}${
            "/ts-proxy?url=" +
            encodeURIComponent(uri.href) +
            "&headers=" +
            encodeURIComponent(JSON.stringify(headers))
          }`
        );
      }
    }

    // Removes headers that are not needed for the client.
    [
      "Access-Control-Allow-Origin",
      "Access-Control-Allow-Methods",
      "Access-Control-Allow-Headers",
      "Access-Control-Max-Age",
      "Access-Control-Allow-Credentials",
      "Access-Control-Expose-Headers",
      "Access-Control-Request-Method",
      "Access-Control-Request-Headers",
      "Origin",
      "Vary",
      "Referer",
      "Server",
      "x-cache",
      "via",
      "x-amz-cf-pop",
      "x-amz-cf-id",
    ].map((header) => res.removeHeader(header));

    // You need these headers so that the client recognizes the response as an m3u8.
    res.setHeader("Content-Type", "application/vnd.apple.mpegurl");
    res.setHeader("Access-Control-Allow-Origin", "*");
    res.setHeader("Access-Control-Allow-Headers", "*");
    res.setHeader("Access-Control-Allow-Methods", "*");

    res.end(newLines.join("\n"));
    return;
  }
}

async function proxyTs(url, headers, req, res) {
  // I love how NodeJS HTTP request client only takes http URLs :D It's so fun!
  // I'll probably refactor this later.

  let forceHTTPS = false;

  if (url.startsWith("https://")) {
    forceHTTPS = true;
  }

  const uri = new URL(url);

  // Options
  // It might be worth adding ...req.headers to the headers object, but once I did that
  // the code broke and I receive errors such as "Cannot access direct IP" or whatever.
  const options = {
    hostname: uri.hostname,
    port: uri.port,
    path: uri.pathname + uri.search,
    method: req.method,
    headers: {
      "User-Agent":
        "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/80.0.3987.132 Safari/537.36",
      ...headers,
    },
  };

  // Proxy request and pipe to client
  try {
    if (forceHTTPS) {
      const proxy = https.request(options, (r) => {
        r.headers["content-type"] = "video/mp2t";
        res.writeHead(r.statusCode ?? 200, r.headers);

        r.pipe(res, {
          end: true,
        });
      });

      req.pipe(proxy, {
        end: true,
      });
    } else {
      const proxy = http.request(options, (r) => {
        r.headers["content-type"] = "video/mp2t";
        res.writeHead(r.statusCode ?? 200, r.headers);

        r.pipe(res, {
          end: true,
        });
      });
      req.pipe(proxy, {
        end: true,
      });
    }
  } catch (e) {
    res.writeHead(500);
    res.end(e.message);
    return null;
  }
}

