const encryptData = async (data) => {
  try {
    const { PUBLIC_KEY } = process.env;
    if (PUBLIC_KEY)
      /*  return await CryptoJS.AES.encrypt(
        JSON.stringify(data),
        PUBLIC_KEY
      ).toString(); */
      return await CryptoJS.AES.encrypt(
        JSON.stringify(data),
        PUBLIC_KEY
      ).toString();
    return false;
  } catch (err) {
    return false;
  }
};

const decryptData = async (data) => {
  try {
    const { PUBLIC_KEY } = process.env;
    if (PUBLIC_KEY) {
      /*  const dataCipher = CryptoJS.AES.decrypt(data, PUBLIC_KEY);
      return await JSON.parse(dataCipher.toString(CryptoJS.enc.Utf8)); */

      const decrypted = await CryptoJS.AES.decrypt(data, PUBLIC_KEY);
      return await JSON.parse(decrypted.toString(CryptoJS.enc.Utf8));
    }
    return false;
  } catch (err) {
    console.log(err.message);
    return false;
  }
};

