app.get("/resources", cors(corsOptionsDelegate), async (req, res) => {
  console.log("API->/reources");
  try {
    const { AUTHORIZATION, SERVICE_TOKEN } = process.env;
    if (AUTHORIZATION && SERVICE_TOKEN) {
      const myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");
      myHeaders.append("Authorization", `Bearer ${AUTHORIZATION}`);

      const requestOptions = {
        method: "GET",
        headers: myHeaders,
        redirect: "follow",
      };

      const year = new Date().getFullYear();
      const month = new Date().getMonth() + 1;

      const startTime = new Date(`${year}-${month}-01`).toISOString();

      const bandwidth = await fetch(
        `https://api.render.com/v1/metrics/bandwidth?startTime=${startTime}&resource=${SERVICE_TOKEN}`,
        requestOptions
      )
        .then((response) => response.json())
        .catch((error) => console.error(error));
      if (!bandwidth) throw new Error("Data bandwidth undefined");

      const values = await bandwidth
        ?.shift()
        ?.values?.map((el) => el.value)
        .reduce((sum, current) => sum + current, 0);
      if (!values) throw new Error("Data values undefined");
      const residual_giga = await Number((values * 0.00097656259).toFixed(2));
      console.log("API->/reources:residual_giga", residual_giga);
      res.json({ data: residual_giga, error: false });
    }
  } catch (err) {
    console.log(err.message);
    res.json({ data: false, error: err.message });
  }
});

app.get("/img-proxy", cors(corsOptionsDelegate), async (req, res) => {
  console.log("API->/proxy", `/proxy/${req.url.split("/img-proxy?")[1]}`);
  const host = process.env.PUBLIC_URL;
  try {
    let extractPath = req?.url?.split("/");
    extractPath?.shift();
    const hostService = extractPath?.shift();
    extractPath = extractPath?.join("/");
    let proxySubPath = hostService?.includes("eurostreaming")
      ? "proxyEurostreamingImg"
      : false;
    proxySubPath = hostService?.includes("altadefinizione")
      ? "proxyAltadefinizioneImg"
      : proxySubPath;
    let newHeaders = {};
    newHeaders["Accept-Encoding"] = "gzip, deflate, br";
    newHeaders["Content-Type"] = "image/jpeg";

    const result = await axios(`${host}/${proxySubPath}/${extractPath}`, {
      headers: newHeaders,
      responseType: "arraybuffer",
    })
      .then((response) =>
        Buffer.from(response.data, "binary").toString("base64")
      )
      .catch((err) => {
        console.log("axios", err.message);
        return null;
      });

    if (!result) throw new Error("img proxy error");

    res.json({ img_url: result });
  } catch (err) {
    res.json({ img_url: false });
  }
});

